import React from 'react'
import quotes from '../assets/img/quotes.png'

export default function RecoPreview({ reco }) {
    return (
        <div className="reco-preview flex">

            <div className="quote-img-container">
                <img src={quotes} alt="quotes-img" />
            </div>

            <div className="reco-info">
                <p>{reco.txt}</p>
                <h4>{reco.from.name}</h4>
                <small>{reco.from.profession}</small>

                <div className="person-img-container">
                    <img src={reco.from.imgUrl} alt="recommander img" />
                </div>
            </div>
        </div>
    )
}
