import React from 'react'

export default function SkillPreview({ skill }) {
    return (
        <div className="skill-preview flex">
            <div className="skill-img-container">
                <img alt="skill-img" src={skill.imgUrl} />
            </div>

            <div className="skill-info">
                <h4>{skill.title}</h4>
                <p>{skill.info}</p>
            </div>
        </div>
    )
}
