import React from 'react'
import RecosList from './RecosList'

export default function CustomerRecommendations() {
    const recos = [
        {
            txt: '“The team perfectly fit the specialized skill set required. They focused on the most essential features helping us launch the platform eight months faster than planned.”',
            from: {
                name: 'Kady Baker',
                profession: 'Product Manager at Bookmark',
                imgUrl: 'https://i.ibb.co/yRnwRTj/avatar-kady.jpg'
            },
        },
        {
            txt: '“We needed to automate our entire onboarding process. The team came in and built out the whole journey. Since going live, user retention has gone through the roof!”',
            from: {
                name: 'Aiysha Reese',
                profession: 'Founder of Manage',
                imgUrl: 'https://i.ibb.co/YpDnc0F/avatar-aiysha.jpg'
            },
        },
        {
            txt: '“Amazing. Our team helped us build an app that delivered a new experience for hiring a physio. The launch was an instant success with 100k downloads in the first month.”',
            from: {
                name: 'Arthur Clarke',
                profession: 'Co-founder of MyPhysio',
                imgUrl: 'https://i.ibb.co/jrF3fK5/avatar-arthur.jpg'
            },
        }
    ]

    return (
        <section className="customer-recs-container full">
            <div className="customer-recs">
                <h1>
                    Delivering real results for top companies. Some of our
                    <span> success stories.</span>
                </h1>

                <RecosList recos={recos} />
            </div>
        </section>
    )
}
